.header {
  font-size: 17px;
  margin:10px;
}

.header > div:nth-child(2){
  margin-left:20px;
}

.hamtainer {
  width: 40px;
  height: 40px;
  margin: 20px;
  cursor: pointer;
}

.mobile-menu {
  position: absolute;
  background: white;
  right:-100%;
  top:0px;
  width: 50%;
  height:100%;
  padding:30px;
  padding-top:100px;
  transition: all .5s;
}

.mobile-menu.open {
  right:0%;
}

.link {
  cursor: pointer;
  font-size: 14px;
  padding: 10px;
}

.active {
  color: blue;
}

img {
  width: 90%;
  margin: auto;
  margin-bottom: 20px;
  display: block;
}